A,sBAAA,CACA,kBAAA,CACA,gBAAA,CACA,WAAA,CACA,eAAA","sourcesContent":["@import url(\"https://fonts.googleapis.com/css2?family=League+Spartan:wght@100;300;600&display=swap\");\n\ncard-restaurant {\n  border: 2px solid black;\n  border-radius: 10px;\n  display: flex;\n  flex-direction: column;\n}\n\n.card__title,\n.card__rating {\n  margin-bottom: 5px;\n}\n.card__image {\n  min-height: 200px;\n  width: 100%;\n}\n\n.card__image img {\n  width: 100%;\n  height: 200px;\n  object-fit: cover;\n  border-radius: 10px 10px 0 0;\n}\n\n.kota {\n  background: rgb(255, 255, 195);\n  font-weight: bold;\n  padding: 8px;\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\n  display: inline-block;\n  transform: translateY(50%);\n  transition: all 0.3s ease-in-out;\n  position: absolute;\n}\n.card__content {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  padding: 20px;\n  min-height: 200px;\n  width: 100%;\n  flex-grow: 1;\n}\n.card__description {\n  line-height: 1.2;\n  text-align: justify;\n}\n.card__footer {\n  display: flex;\n  margin-top: 5px;\n  flex-grow: 1;\n  align-self: flex-end;\n  align-items: flex-end;\n  gap: 5px;\n}\n\n.card__footer button,\n.card__footer a {\n  background: rgb(255, 255, 195);\n  font-weight: bold;\n  padding: 3px;\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\n  display: inline-block;\n  border-radius: 5px;\n  transition: all 0.3s ease-in-out;\n  min-width: 45px;\n  min-height: 45px;\n  font-size: 1em;\n  border: white;\n}\n\n.card__footer img {\n  width: 30px;\n}\n\n.card__footer button:active,\n.card__footer a:active {\n  background: rgb(233, 93, 53);\n  color: rgb(255, 255, 195);\n  box-shadow: 2px 4px 2px rgb(255, 255, 195);\n  transition: none;\n}\n\n/* detail restaurant */\n#detail-img {\n  border-radius: 10px 10px 10px 10px;\n}\n\n#restaurant {\n  padding: 20px;\n  min-height: 200px;\n  overflow-x: hidden;\n  max-width: 1000px;\n}\n\n#btn__container {\n  display: flex;\n  flex-direction: row;\n  gap: 10px;\n  list-style: none;\n  max-width: 600px;\n  margin-top: 5px;\n  margin-left: 10px;\n}\n\n.tab_container {\n  width: 100%;\n  min-height: 200px;\n  overflow-x: hidden;\n  max-width: 1000px;\n}\n\n.btn_tab_pane {\n  display: inline;\n  padding: 10px;\n  border: 1px none;\n  font-weight: bold;\n  border-top-left-radius: 10px;\n  border-top-right-radius: 10px;\n  cursor: pointer;\n  min-width: 45px;\n  min-height: 45px;\n  font-size: 1em;\n  background: rgb(255, 255, 195);\n  box-shadow: 2px -4px 2px rgb(255, 197, 92);\n  transition: all 0.3s ease-in-out;\n}\n.active {\n  background: rgb(233, 93, 53);\n  color: rgb(255, 255, 195);\n  box-shadow: 2px -4px 2px rgb(255, 255, 195);\n  transition: none;\n}\n\n.tab {\n  border: 2px solid black;\n  border-radius: 10px;\n  padding: 10px;\n  transition: 0.3s ease-in-out;\n  /* flex-grow: 1; */\n}\n.none {\n  display: none;\n}\n\n/* menu */\n.menu {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  justify-content: space-between;\n  gap: 10px;\n  list-style: none;\n  max-width: 600px;\n  margin-top: 5px;\n  margin-left: 10px;\n  line-height: 1.2em;\n}\n\n/* review */\n.review__item {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  padding: 7px;\n  width: 100%;\n  margin-bottom: 4px;\n  border-bottom: 1px solid black;\n}\n\n.tanggal {\n  font-weight: lighter;\n}\n\n.add__review {\n  padding: 7px;\n  width: 100%;\n  margin-bottom: 4px;\n  border-bottom: 2px dashed rgb(233, 93, 53);\n}\n\n.add_review_form {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: flex-start;\n  gap: 2px;\n  padding: 7px;\n  width: 100%;\n}\n\n.add__review__input {\n  min-width: 45px;\n  min-height: 45px;\n  border-radius: 10px;\n  border: none;\n  padding: 5px;\n  outline: none;\n  font-size: 1em;\n}\n\n.add_review_btn {\n  background: rgb(255, 255, 195);\n  padding: 5px;\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\n  display: inline-block;\n  border-radius: 5px;\n  transition: all 0.3s ease-in-out;\n  min-width: 45px;\n  min-height: 45px;\n  font-size: 1em;\n  border: white;\n}\n.add_review_btn:active {\n  background: rgb(233, 93, 53);\n  color: rgb(255, 255, 195);\n  box-shadow: 2px 4px 2px rgb(255, 255, 195);\n  transition: none;\n}\n\n/* category */\n.category ul {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: no-wrap;\n  justify-content: flex-end;\n  gap: 10px;\n  list-style: none;\n  margin: 5px;\n  margin-bottom: 10px;\n  line-height: 1.12em;\n}\n\n.category__item {\n  background: rgb(255, 255, 195);\n  padding: 5px;\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\n  display: inline-block;\n  border-radius: 5px;\n  transition: all 0.3s ease-in-out;\n  min-width: 45px;\n  min-height: 45px;\n  font-size: 1em;\n  border: white;\n  text-decoration: none;\n  padding: 15px;\n}\n\n.toast {\n  position: fixed;\n  top: 60px;\n  right: 0;\n  padding: 5px;\n  display: inline-block;\n  background: white;\n  border-radius: 5px;\n  transition: all 0.3s ease-in-out;\n  min-width: 45px;\n  min-height: 45px;\n  font-size: 1em;\n  text-decoration: none;\n  padding: 15px;\n  z-index: 99;\n  margin: 10px;\n  cursor: pointer;\n  animation-name: loading-toast-time;\n  animation-duration: 4s;\n}\n\n.success {\n  --color: hsl(108, 76%, 50%);\n  --shadow-color: hsl(108, 76%, 80%);\n}\n\n.error {\n  --color: hsl(10, 76%, 50%);\n  --shadow-color: hsl(0, 76%, 80%);\n}\n\n.info {\n  --color: hsl(200, 76%, 50%);\n  --shadow-color: hsl(200, 76%, 80%);\n}\n\n@keyframes loading-toast-time {\n  25% {\n    border-top: 3px solid var(--color);\n  }\n  50% {\n    border-right: 3px solid var(--color);\n  }\n  75% {\n    border-bottom: 3px solid var(--color);\n  }\n  100% {\n    border-left: 3px solid var(--color);\n    box-shadow: 0 0 10px var(--shadow-color);\n  }\n}\n\n.loading {\n  z-index: 999;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  gap: 10px;\n}\n.loading__container {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  gap: 10px;\n  min-height: 50px;\n}\n.loading__item {\n  width: 20px;\n  height: 40px;\n  border-radius: 10px;\n  background: grey;\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n}\n\n.loading__item:nth-child(1) {\n  animation: loading 1s infinite;\n}\n.loading__item:nth-child(2) {\n  animation: loading 1s infinite 0.2s;\n}\n.loading__item:nth-child(3) {\n  animation: loading 1s infinite 0.4s;\n}\n\n@keyframes loading {\n  0% {\n    height: 40px;\n  }\n  50% {\n    height: 20px;\n  }\n  100% {\n    height: 40px;\n  }\n}\n\n#likeButtonContainer {\n  position: fixed;\n  bottom: 0;\n  right: 0;\n  margin: 30px;\n  z-index: 99;\n}\n\n.like {\n  background: rgb(255, 255, 195);\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\n  display: inline-block;\n  border-radius: 5px;\n  transition: all 0.3s ease-in-out;\n  min-width: 45px;\n  min-height: 45px;\n  font-size: 1em;\n  border: white;\n  text-decoration: none;\n  padding: 3px;\n  cursor: pointer;\n}\n\n.like img {\n  width: 30px;\n}\n\n.empty {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  min-height: 200px;\n  margin: 10px;\n  font-size: large;\n}\n"],"sourceRoot":""}]);
