px;\n    font-size: 1.2rem;\n    margin: 10px 0;\n\n    li {\n      a {\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        text-decoration: none;\n        color: $red-orange;\n        transition: all 0.3s ease-in-out;\n        font-weight: 600;\n        min-width: 45px;\n        min-height: 45px;\n        &:hover {\n          color: $yellow-light;\n        }\n      }\n    }\n  }\n}\n\nmain {\n  display: flex;\n  justify-content: center;\n  article {\n    #hero {\n      display: flex;\n      flex-direction: column;\n      gap: 20px;\n      justify-content: center;\n      align-items: center;\n      text-align: center;\n      min-height: 400px;\n      width: 100vw;\n      max-width: 1200px;\n      background-image: url(\"../public/images/heros/hero-image_4-large.jpg\");\n      background-image: url(\"../public/images/heros/hero-image_4-large.webp\");\n      background-color: $red-orange;\n      background-size: cover;\n      background-position: center;\n      background-repeat: no-repeat;\n      padding: 20px;\n      margin: 0px;\n\n      h1 {\n        color: $red-orange;\n        font-size: 4rem;\n        text-shadow: 2px 4px 2px rgba(255, 255, 0, 1);\n      }\n\n      .title-2 {\n        font-size: 2.5rem;\n      }\n\n      #search-restaurant {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        gap: 20px;\n        padding: 20px;\n        margin: 0px;\n        max-width: 1200px;\n        font-size: 1.2rem;\n        text-align: center;\n        font-weight: 600;\n        margin-top: auto;\n        border-radius: 20px;\n        opacity: 0.75;\n      }\n    }\n\n    #restaurant-container {\n      display: grid;\n      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));\n      grid-gap: 20px;\n      padding: 20px;\n      margin: 0px;\n      width: 100vw;\n      max-width: 1200px;\n    }\n  }\n}\n\nfooter {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: $red-orange;\n  color: $yellow-light;\n  padding: 20px;\n  margin: 0px;\n  min-height: 50px;\n  width: 100vw;\n  font-size: 1.2rem;\n  text-align: center;\n  font-weight: 600;\n  margin-top: auto;\n}\n\n@media screen and (max-width: 425px) {\n  .nav {\n    flex-direction: column;\n\n    .nav__logo {\n      min-width: 100vw;\n    }\n\n    .hamburger {\n      display: inline !important ;\n      font-size: 2rem;\n      color: $red-orange;\n      border: none;\n      background: none;\n      transform: translateY(0%) !important;\n    }\n    .nav-menu {\n      position: absolute;\n      transform: translateY(-100%);\n      z-index: 998;\n      background: $orange;\n      padding: 5px;\n      margin: 0px;\n      min-width: 100vw;\n      justify-content: space-around;\n    }\n    .nav-menu.open {\n      transform: translateY(100%);\n      transition: transform 0.4s ease-in-out;\n    }\n  }\n  main {\n    article {\n      #hero {\n        background-image: url(\"../public/images/heros/hero-image_4-small.jpg\");\n        background-image: url(\"../public/images/heros/hero-image_4-small.webp\");\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
